export default {
	components: {},
	destroyed() {

	},
	data() {
		return {
			warefocus: false,
			ware: "",

			bigfocus: false,
			big: "",

			ErrorRemind: "",
			InWareHouseNumber: 0,
			bigdisabled: "disabled"
		}
	},
	onLoad() {
		this.warefocus = true;
		this.ware = '';

		this.bigfocus = false;
		this.big = '';

		this.ErrorRemind = '';
		this.InWareHouseNumber = 0;
		this.bigdisabled = "disabled";
	},
	onShow() {
		this.warefocus = true;
		this.ware = '';

		this.bigfocus = false;
		this.big = '';

		this.ErrorRemind = '';
		this.InWareHouseNumber = 0;
		this.bigdisabled = "disabled";
	},
	methods: {
		focus_ware() {
			if (this.ware.length > 0) {
				this.ware = '';
			}
			setTimeout(() => {
				uni.hideKeyboard();
			}, 500);
		},
		focus_big() {
			setTimeout(() => {
				uni.hideKeyboard();
			}, 500);
		},
		//验证入库单
		ware_confirm() {
			var ware = this.ware.trim();
			console.log(ware);
			if (ware.length < 1) {
				this.bigdisabled = "disabled";
				return;
			}
			var user = uni.getStorageSync('userName');
			var token = uni.getStorageSync('token');
			var Url = this.$websiteUrl + '/api/mstoragelocation/get?storagelocation=' + ware + '&user=' + user + '&token=' +
				token;
			uni.request({
				url: Url,
				method: 'GET',
				//data: {username:User_Value,password:Pass_Value},
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				success: res => {
					console.log('请求回执:' + JSON.stringify(res.data));
					if (res.data.code == '999') {
						uni.navigateTo({
							url: '/pages/zaizai-login/index'
						});
					}
					if (res.data.code == '200') {
						this.warefocus = false;
						this.bigfocus = true;
						this.ErrorRemind = '';
						//if(this.mawb.length > 0 && this.ware.length > 0)
						//{
						//	this.bigdisabled = false;
						//}
					} else {
						this.ErrorRemind = res.data.text;
						this.ware = '';
					}
				},
				fail: failres => {
					this.ErrorRemind = "请求异常";
					return;
				},
				complete: () => {}
			});
		},
		big_confirm() {
			var big = this.big.trim();
			var ware = this.ware.trim();
			console.log(big);
			if (ware.length < 1) {
				return;
			}

			var Url = this.$websiteUrl + '/api/mpackage/MoveStoragelocation';
			var user = uni.getStorageSync('userName');
			var token = uni.getStorageSync('token');
			uni.request({
				url: Url,
				method: 'POST',
				data: {
					assbillno: big,
					storagelocation: ware,
					user: user,
					token: token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				success: res => {
					console.log('请求回执:' + JSON.stringify(res.data));
					if (res.data.code == '999') {
						uni.navigateTo({
							url: '/pages/zaizai-login/index'
						});
					}
					if (res.data.code == '200') {
						if (res.data.TempCode == '200') //大包移库
						{
							uni.showModal({
								//title: '提示',
								content: '是否确认将整个大包进行移库？',
								//success: function (res) {
								success: res => {
									if (res.confirm) {
										//console.log('用户点击确定');

										var url = this.$websiteUrl + '/api/mbag/MoveStoragelocation';



										uni.request({
											//url: 'http://www.hfpretty.top/Index/UpDateSupport',
											url: url,
											method: 'POST',
											data: {
												bagid: big,
												storagelocation: ware,
												user:user,
												token:token
											},
											header: {
												'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
											},
											success: res => {
												console.log(res);
												if (res.data.code == '200') {
													this.warefocus = false;
													this.bigfocus = true;
													this.ErrorRemind = '';
													this.InWareHouseNumber = this.InWareHouseNumber + 1;

													if (this.ware.length > 0) {
														this.bigdisabled = false;
													}
												} else {

													this.ErrorRemind = res.data.text;
												}


											},
											fail: () => {},
											complete: () => {}
										});
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								},
								fail: () => {
									console.log('异常');
								},
							});
						} else {
							this.warefocus = false;
							this.bigfocus = true;
							this.ErrorRemind = '';
							this.InWareHouseNumber = this.InWareHouseNumber + 1;

							if (this.ware.length > 0) {
								this.bigdisabled = false;
							}
						}

					} else {
						this.ErrorRemind = res.data.text;
					}

					this.big = '';
				},
				fail: failres => {
					this.ErrorRemind = "请求异常";
					return;
				},
				complete: () => {}
			});


		},
		ware_blur() {
			if (this.ware.length > 0) {
				this.bigdisabled = false;
			} else {
				this.bigdisabled = 'disabled';
			}
		}


		//mawb_Clear()
		//{
		// this.mawbfocus = true;
		// this.warefocus = false;
		// this.bigfocus = false;
		// this.mawb = "";
		// this.InWareHouseNumber = 0;
		//},
		//ware_Clear()
		//{
		// this.mawbfocus = false;
		// this.warefocus = true;
		// this.bigfocus = false;
		// 
		// this.ware = "";
		//}
		// big_Clear()
		// {
		// 	this.mawbfocus = false;
		// 	this.warefocus = false;
		// 	this.bigfocus = true;
		// 	
		// 	this.big = "";
		// }
	}
}
